        },
        getImage(val) {
            if (val == 1) {
                return require('@/assets/img/files/folder.png');
            } else if (val == 2) {
                return require('@/assets/img/files/excel.png');
            } else if (val == 3) {
                return require('@/assets/img/files/pdf.png');
            } else if (val == 4) {
                return require('@/assets/img/files/word.png');
            }
        },
        getImage2(name) {
            return `https://via.placeholder.com/40?text=${name[0]}`;
        },
        selectLastName(item, option) {
            // Update the item's last_name when a new option is selected
            item.last_name = option;
        },
        // scrollHanle(evt) {
        //     // console.log(evt)
        // },
        ProofOfMeansYNOnChange() {
            if (this.create_case_model.ProofOfMeansYN) {
                this.proofOfMeansCreateState = true;
            } else {
                this.proofOfMeansCreateState = false;
            }
        },
        AgeDisputeYNOnChange() {
            if (this.create_case_model.AgeDisputeYN) {
                this.ageDisputeCreateState = true;
            } else {
                this.ageDisputeCreateState = false;
            }
        },
        ConflictOfInterestYNOnChange() {
            if (this.create_case_model.ConflictOfInterestYN) {
                this.conflictOfInterestCreateState = true;
            } else {
                this.conflictOfInterestCreateState = false;
            }
        },
        ProofOfMeansYNOnChangeUpdate() {
            if (this.model.ProofOfMeansYN) {
                this.proofOfMeansState = true;
            } else {
                this.proofOfMeansState = false;
            }
        },
        AgeDisputeYNOnChangeUpdate() {
            if (this.model.AgeDisputeYN) {
                this.ageDisputeState = true;
            } else {
                this.ageDisputeState = false;
            }
        },
        ConflictOfInterestYNOnChangeUpdate() {
            if (this.model.ConflictOfInterestYN) {
                this.conflictOfInterestState = true;
            } else {
                this.conflictOfInterestState = false;
            }
        },
        setValuesToModelData(data) {
            this.model.Id = data.Id;
            this.model.CaseRefNo = data.CaseRefNo;
            this.model.Title = data.Title;
            this.model.CaseTypeId = data.CaseType && data.CaseType.Id ? data.CaseType.Id : '';
            this.model.CaseType = data.CaseType;  // NEED TO SET VALUES TO DROPDOWN

            if (data.ProofOfMeans == 1) {
                this.model.ProofOfMeansYN = true;
                this.proofOfMeansState = true;
            } else {
                this.model.ProofOfMeansYN = false;
                this.proofOfMeansState = false;
            }

            if (data.ConflictOfInterestYN) {
                this.model.ConflictOfInterestYN = true;
                this.conflictOfInterestState = true;
            } else {
                this.model.ConflictOfInterestYN = false;
                this.conflictOfInterestState = false;
            }

            if (data.AgeDisputeYN) {
                this.model.AgeDisputeYN = true;
                this.ageDisputeState = true;
            } else {
                this.model.AgeDisputeYN = false;
                this.ageDisputeState = false;
            }

            this.model.AgeDisputeResolved = data.AgeDisputeResolved;
            this.model.CaseHORefNo = data.CaseHORefNo;
            this.model.LasteUpdatedDate = data.LasteUpdatedDate;
            this.model.LastUpdatedUser = data.LastUpdatedUser;

            this.model.ClientId = data.ClientId;
            this.model.ClientName = data.ClientInfo ? (data.ClientInfo.FirstName + ' ' + data.ClientInfo.LastName) : '';
            this.model.clientAlias = data.ClientInfo ? data.ClientInfo.Alias : '';
            this.model.clientDOB = data.ClientInfo && data.ClientInfo.DOB ? data.ClientInfo.DOB : '';
            this.model.clientPostalCode = data.ClientInfo && data.ClientInfo.PostalCode ? data.ClientInfo.PostalCode : '';
            this.model.clientSate = data.ClientInfo && data.ClientInfo.State ? data.ClientInfo.State : '';
            this.model.clientCountry = data.ClientInfo && data.ClientInfo.CountryId ? data.ClientInfo.CountryId : '';
            this.model.clientAddres1 = data.ClientInfo && data.ClientInfo.Address1 ? data.ClientInfo.Address1 : '';
            this.model.clientAddres2 = data.ClientInfo && data.ClientInfo.Address2 ? data.ClientInfo.Address2 : '';
            this.model.clientSecondaryNo = data.ClientInfo && data.ClientInfo.SecodaryContactNo ? this.formattedPhoneNumber(data.ClientInfo.SecodaryContactNo) : '';
            this.model.clientPrimaryNo = data.ClientInfo && data.ClientInfo.PrimaryContactNo ? this.formattedPhoneNumber(data.ClientInfo.PrimaryContactNo) : '';
            this.model.clientInstantMessagingNo = data.ClientInfo && data.ClientInfo.InstantMessagingNo ? this.formattedPhoneNumber(data.ClientInfo.InstantMessagingNo) : '';

            this.model.clientPin = data.ClientInfo && data.ClientInfo.PIN ? data.ClientInfo.PIN : '';
            this.model.clientInfoData = data.ClientInfo ? data.ClientInfo : {};

            this.model.FosterCarerName = data.ClientInfo && data.ClientInfo.FosterCarerName ? data.ClientInfo.FosterCarerName : '';
            this.model.FosterCarerAddress = data.ClientInfo && data.ClientInfo.FosterCarerAddress ? data.ClientInfo.FosterCarerAddress : '';
            this.model.FosterCarerEmail = data.ClientInfo && data.ClientInfo.FosterCarerEmail ? data.ClientInfo.FosterCarerEmail : '';
            this.model.FosterCarerContactNo = data.ClientInfo && data.ClientInfo.FosterCarerContactNo ? data.ClientInfo.FosterCarerContactNo : '';

            this.model.SocialWorkerName = data.ClientInfo && data.ClientInfo.SocialWorkerName ? data.ClientInfo.SocialWorkerName : '';
            this.model.SocialWorkerAddress = data.ClientInfo && data.ClientInfo.SocialWorkerAddress ? data.ClientInfo.SocialWorkerAddress : '';
            this.model.SocialWorkerEmail = data.ClientInfo && data.ClientInfo.SocialWorkerEmail ? data.ClientInfo.SocialWorkerEmail : '';
            this.model.SocialWorkerContactNo = data.ClientInfo && data.ClientInfo.SocialWorkerContactNo ? data.ClientInfo.SocialWorkerContactNo : '';

            this.model.ProofOfMeansVal = data && data.ProofOfMeansVal ? data.ProofOfMeansVal : '';
            this.model.ConflictOfInterestVal = data && data.ConflictOfInterestVal ? moment(data.ConflictOfInterestVal).format('YYYY-MM-DD') : '';
            this.model.AgeDisputeVal = data && data.AgeDisputeVal ? data.AgeDisputeVal : ''
        },
        onSingleCaseClick(data) {
            if (data) {
                this.showEditSection = true;
                this.isSidebarVisible = false;
                this.resetCreateValidations();
                this.resetUpdateValidations();
                this.setValuesToModelData(data);
            }
        },
        openAddNewCaseModal() {
            this.isSidebarVisible = true;
            this.resetCreateValidations();
            this.resetUpdateValidations();
            this.resetCreateNewModel();
        },
        resetCreateNewModel() {
            this.create_case_model = {
                Id: '',
                CaseRefNo: '',
                Title: '',
                CaseTypeId: '',
                CaseType: '',
                ClientId: '',
                ClientName: '',
                ProofOfMeansYN: '',
                ConflictOfInterestYN: '',
                AgeDisputeYN: '',
                AgeDisputeResolved: '',
                CaseHORefNo: '',
                LasteUpdatedDate: '',
                LastUpdatedUser: '',
            }
        },
        resetUpdateDataModel() {
            this.model = {
                Id: '',
                CaseRefNo: '',
                Title: '',
                CaseTypeId: '',
                CaseType: '',
                ClientId: '',
                ClientName: '',
                ProofOfMeansYN: '',
                ConflictOfInterestYN: '',
                AgeDisputeYN: '',
                AgeDisputeResolved: '',
                CaseHORefNo: '',
                LasteUpdatedDate: '',
                LastUpdatedUser: '',
            }
        },
        resetCreateValidations() {
            this.errors = {
                titleValidation: false,
                caseTypeValidation: false,
                clientValidation: false,
                ProofOfMeansYNValidation: false,
                ConflictOfInterestYNValidation: false,
                AgeDisputeYNValidation: false,
            }
        },
        resetUpdateValidations() {
            this.errorsUpdate = {
                titleValidation: false,
                caseTypeValidation: false,
                clientValidation: false,
                ProofOfMeansYNValidation: false,
                ConflictOfInterestYNValidation: false,
                AgeDisputeYNValidation: false,
            }
        },
        formatDateToISO(date) {
            // Convert 'DD/MM/YYYY' to 'YYYY-MM-DD'
            const [day, month, year] = date.split('/');
            return `${year}-${month}-${day}`;
        },
        async createNewCase(e) {
            e.preventDefault();

            this.resetCreateValidations();
            this.resetUpdateValidations();
            this.loading = true;
            let validate = this.checkValidation('save');

            if (validate) {
                let reqBody = {
                    Title: this.create_case_model.Title,
                    CaseTypeId: this.create_case_model.CaseTypeId,
                    ClientId: this.create_case_model.ClientId,
                    ProofOfMeans: this.create_case_model.ProofOfMeansYN,
                    ConflictOfInterestYN: this.create_case_model.ConflictOfInterestYN,
                    AgeDisputeYN: this.create_case_model.AgeDisputeYN,
                    AgeDisputeResolved: this.create_case_model.AgeDisputeResolved,
                    CaseHORefNo: this.create_case_model.CaseHORefNo,
                    ProofOfMeansVal: this.create_case_model.ProofOfMeansVal,
                    ConflictOfInterestVal: this.create_case_model.ConflictOfInterestVal,
                    AgeDisputeVal: this.create_case_model.AgeDisputeVal,
                    IsNewClient: this.create_case_model.IsNewClient,
                    FirstName: this.create_case_model.FirstName,
                    LastName: this.create_case_model.LastName,
                    Alias: this.create_case_model.Alias,
                    DOB: this.create_case_model.DOB,
                    Language: this.create_case_model.Language,
                    PrimaryContactNo: this.create_case_model.PrimaryContactNo,
                    Address1: this.create_case_model.Address1,
                    Address2: this.create_case_model.Address2,
                    State: this.create_case_model.State,
                    CountryId: this.create_case_model.CountryId,
                    PostalCode: this.create_case_model.PostalCode
                }

                try {
                    let response = await axios.post(`http://127.0.0.1:5000/api/case-info/create`, reqBody);

                    if (response && response.data && response.data.success) {
                        this.isSidebarVisible = false;
                        this.$bvToast.toast('Case created successfully', {
                            title: 'Success!',
                            variant: 'success',
                            noAutoHide: false,
                            autoHideDelay: 1000,
                            solid: true
                        });
                        setTimeout(() => {
                            this.getCaseInfo();
                        }, 1000);
                    } else {
                        this.$bvToast.toast('Please retry or contact administrator', {
                            title: 'Error!',
                            variant: 'danger',
                            noAutoHide: false,
                            autoHideDelay: 1000,
                            solid: true
                        });
                        setTimeout(() => {
                            this.getCaseInfo();
                        }, 1000);
                    }
                } catch (err) {
                    this.error = 'Error updating case info';
                } finally {
                    this.loading = false;
                }
            } else {
                this.loading = false;
                console.log("errorrr");
            }
        },
        async updateCase(e) {
            e.preventDefault();

            this.resetCreateValidations();
            this.resetUpdateValidations();
            this.loading = true;
            this.isSidebarVisible = false;
            let validate = this.checkValidation('update');

            if (validate) {
                let reqBody = {
                    Id: this.model.Id,
                    CaseRefNo: this.model.CaseRefNo,
                    Title: this.model.Title,
                    CaseTypeId: this.model.CaseTypeId,
                    ClientId: this.model.ClientId,
                    ProofOfMeans: this.model.ProofOfMeansYN,
                    ConflictOfInterestYN: this.model.ConflictOfInterestYN,
                    AgeDisputeYN: this.model.AgeDisputeYN,
                    AgeDisputeResolved: this.model.AgeDisputeResolved,
                    CaseHORefNo: this.model.CaseHORefNo,
                    ProofOfMeansVal: this.model.ProofOfMeansVal,
                    ConflictOfInterestVal: this.model.ConflictOfInterestVal,
                    AgeDisputeVal: this.model.AgeDisputeVal,
                }

                try {
                    let response = await axios.put(`http://127.0.0.1:5000/api/case-info/${this.model.Id}`, reqBody);

                    if (response && response.data && response.data.success) {
                        this.$bvToast.toast('Case updated successfully', {
                            title: 'Success!',
                            variant: 'success',
                            noAutoHide: false,
                            autoHideDelay: 1000,
                            solid: true
                        });
                        setTimeout(() => {
                            this.getCaseInfo();
                        }, 1000);
                    } else {
                        this.$bvToast.toast('Please retry or contact administrator', {
                            title: 'Error!',
                            variant: 'danger',
                            noAutoHide: false,
                            autoHideDelay: 1000,
                            solid: true
                        });
                        setTimeout(() => {
                            this.getCaseInfo();
                        }, 1000);
                    }
                } catch (err) {
                    this.error = 'Error updating case info';
                } finally {
                    this.loading = false;
                }
            } else {
                this.loading = false;
                console.log("errorrr");
            }
        },
        checkValidation(type) {
            let status = false;

            if (type === 'save') {
                if (this.create_case_model.Title == '' || this.create_case_model.Title == undefined) {
                    this.errors.titleValidation = true;
                } else {
                    status = true;
                    this.errors.titleValidation = false;
                }

                if (this.create_case_model.CaseTypeId == '' || this.create_case_model.CaseTypeId == undefined) {
                    this.errors.caseTypeValidation = true;
                } else {
                    status = true;
                    this.errors.caseTypeValidation = false;
                }

                // if (this.create_case_model.IsNewClient && (this.create_case_model.IsNewClient == false) && (this.create_case_model.ClientId == '' || this.create_case_model.ClientId == undefined)) {
                //     this.errors.clientValidation = true;
                // } else {
                //     status = true;
                //     this.errors.clientValidation = false;
                // }

                if (!this.create_case_model.IsNewClient) {
                    this.create_case_model.IsNewClient = false;
                } else {
                    this.create_case_model.IsNewClient = true;
                }
            }

            if (type === 'update') {
                if (this.model.Title.trim() == '' || this.model.Title == undefined) {
                    this.errorsUpdate.titleValidation = true;
                } else {
                    status = true;
                    this.errorsUpdate.titleValidation = false;
                }

                if (this.model.CaseTypeId.trim() == '' || this.model.CaseTypeId == undefined) {
                    this.errorsUpdate.caseTypeValidation = true;
                } else {
                    status = true;
                    this.errorsUpdate.caseTypeValidation = false;
                }

                // if (this.model.AgeDisputeResolved.trim() == '' || this.model.AgeDisputeResolved == undefined) {
                //     this.errorsUpdate.AgeDisputeResolved = true;
                // } else {
                //     status = true;
                //     this.errorsUpdate.AgeDisputeResolved = false;
                // }

                // if (this.model.CaseHORefNo.trim() == '' || this.model.CaseHORefNo == undefined) {
                //     this.errorsUpdate.CaseHORefNo = true;
                // } else {
                //     status = true;
                //     this.errorsUpdate.CaseHORefNo = false;
                // }

                // if (this.model.ClientId == '' || this.model.ClientId == undefined) {
                //     this.errorsUpdate.countryValidation = true;
                // } else {
                //     this.errorsUpdate.countryValidation = false;
                // }
            }

            return status;
        },
        formattedPhoneNumber(number) {
            if (number) {
                let phone = number.replace(/\D/g, '');

                if (phone.length === 10) {
                    phone = phone.replace(/(\d{3})(\d{3})(\d{4})/, '($1) $2-$3');
                } else if (phone.length > 10) {
                    let getOddLength = parseInt(phone.length) - 10;
                    const phoneFormatRegex = new RegExp(`(\\d{3})(\\d{3})(\\d{${getOddLength}})`);
                    phone = phone.replace(phoneFormatRegex, '($1) $2-$3');
                }
                return phone;
            }
        },
        addNewClientFunc(e) {
            e.preventDefault();
            this.showSearchClient = false;
            this.create_case_model.IsNewClient = true;
        },
        searchClientFunc(e) {
            e.preventDefault();
            this.showSearchClient = true;
            this.create_case_model.IsNewClient = false;
        }
    },
    beforeMount() {
        this.appOptions.appSidebarMinified = true
    },

}
</script>

